<<template>
  <div id="chat-view">
    <div>
      <!-- <nav>
        <div class="container">
          <a href=''>
            VUECHAT
          </a>
          <ul class="nav__left">
          </ul>
          <ul class="nav__right">
            <a href='' @click="logOut()">SIGNOUT</a>
          </ul>
        </div>
      </nav> -->
      <!-- <main>
        <img src="../assets/logo.png" alt="Vue.js PWA">
        <router-view></router-view>
      </main> -->
    </div>
    <div>
    <div class="top-bar">
      <div class="container">
        <div class="">
          <div class="logo-part">
            <img src="../assets/thachat.svg" alt="Tha Chat" style="width: 60px;">
          </div>
          <div class="logout-part">
            <a @click="logOut()">
              <img class="logout-img" src="../assets/logout.svg" alt="">
            </a>
          </div>
        </div>
      </div>
    </div>

      <div class="chatbox">
        <div class="chatlogs"> 
          <div class="container">
            <div class="media mt-2" v-for="item in anArray">
              <img class="mr-3" v-bind:src="item.photo_url" alt="">
              <div class="media-body">
                <p class="mt-0 author">{{item.name}}</p>
                <p class="message mb-0">{{item.message}}</p>
                <p class="mt-0 author text-right">{{item.timestamp | formatDate}}</p>
              </div>
            </div>
          </div>
          <!-- <div v-for="item in anArray">
            <span class="chat-name">{{item.name}}</span>
            <div class="chat friend">
              <img class="user-photo" v-bind:src="item.photo_url" />
              <div class="chat-message">
                <p>{{item.message}} </p>
                <p>
                  <span class="chat-time"> {{item.timestamp | formatDate}}</span>
                </p>
              </div>
            </div>
          </div> -->
        </div>
        <form @submit.prevent="addComment" class="form-below">
					<div class="chat-form form-group">
						<textarea class="form-control form-space" rows="1"  v-model="newComment" placeholder="Type Message"></textarea>
						<button class="btn btn-send"><span class="fa fa-send"></span></button>
					</div>
        </form>
      </div>

    </div>

</div>
</template>

<script>
/*eslint-disable*/
import firebase, { chatRef } from '../firebase/index';
import Vue from 'vue';
import Vuefire from 'vuefire';
import dateFilter from '../utils/filter.js';
import moment from 'moment';
Vue.use(Vuefire);
export default {
	name: 'chat',
	data() {
		return {
			comment: '',
			newComment: '',
			loading: true,
		};
	},

	firebase: {
		// can bind to either a direct Firebase reference or a query

		anArray: chatRef,

		// optionally provide the cancelCallback
		cancelCallback: function() {},
		// this is called once the data has been retrieved from firebase
		readyCallback: function() {},
	},
	filters: {
		dateFilter,
	},
	methods: {
		logOut() {
			firebase.auth().signOut();
		},

		addComment() {
			if (this.newComment !== '') {
				this.comment = this.newComment.trim();
				chatRef.push({
					message: this.comment,
					name: this.getUserName(),
					timestamp: moment().unix(),
					userId: this.getUserId(),
					photo_url: this.getPhotoURL(),
				});
				this.newComment = '';
			}
		},
		getUserId() {
			return firebase.auth().currentUser.uid;
		},
		getUserName() {
			return firebase.auth().currentUser.displayName;
		},
		getPhotoURL() {
			return firebase.auth().currentUser.photoURL;
		},
	},
};
</script>
<style>
#chat-view {
	background: linear-gradient(#fff6f9, #ffe1eb);
	height: 100vh;
}
.form-below {
    position: fixed;
    width: 100%;
    height: 59px;
    bottom: 0px;
    background-color: black;
}
.form-space:focus{
	background-color: black;
	border: 0px;
}
.form-space{
	border: 0px;
    background: transparent;
    width: 100%;
    border-radius: 0px;
    bottom: 0px;
    position: absolute;
    height: 59px;
}
.btn-send{
	position: absolute;
    right: 20px;
    bottom: 28px;
    width: 60px;
    height: 60px;
    border-radius: 70px;
    color: white;
    border: 3px solid black;
    background: linear-gradient(45deg,#c58a8a,#ff0413);
}
.btn-send:focus{
	border: 0px;
	background: linear-gradient(45deg,#c58a8a,#ff0413);
}
.chatlogs{
	max-height: 78vh;
  overflow-y: scroll;
  /*border-bottom: 2px solid;*/
}
.media img{
  width: 55px;
  border-radius: 39px;
  border: 3px solid #00000026;
}
.author{
    font-style: italic;
    margin-bottom: 0px;
    color: #ffffff96;
    font-size: 0.75em;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
}
.message{
font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
}
.media-body{
  background-color: white;
    padding: 8px 11px;
    border-radius: 7px;
    border-top-left-radius: 0px;
    background: linear-gradient(45deg,#a74646,#ff0413);
    color: white;
}
.logo-n{
   width: 60px;
}
.top-bar{
  padding: 12px 0px;
  box-shadow: 0px 4px 11px #0000004f;
}
.logo-part{
  display: inline-block;
}
.logout-img{
  width: 20px;
  width: 38px;
  margin-top: 12px;
}
.logout-part{
  display: inline-block;
  float: right;
}
.logout-part button{
  background: transparent;
  border: 0px;
}
</style>



