<template>
  <div id="app">
    <div>
      <!-- <nav>
        <div class="container">
          <a href=''>
          VUECHAT
        </a>
          <ul class="nav__left">
          </ul>
          <ul class="nav__right">
            <a href=''>SIGNUP</a>
          </ul>
        </div>
      </nav> -->
      <main>
        <div class="text-center">
          <img class="logo-n" src="../assets/thachat.svg" alt="Vue.js PWA">
        </div>
        <router-view></router-view>
      </main>
    </div>
    <div id="login">
      <div class="container">
        <div class="">
          <div class="login-clean text-center">
            <form>
              <!-- <h1 class ="text">VUECHAT APP</h1> -->
              <h1>{{ msg }}</h1>
              <h3>Login with</h3><hr>
              <div class="form-group">
                <button type="button" class="btn btn-primary btn-twitter"  @click="twitterAuth()"><span class="fa fa-twitter"></span></button>
                <button type="button" class="btn btn-primary btn-facebook"><span class="fa fa-facebook"></span></button>
                <button type="button" class="btn btn-primary btn-google"><span class="fa fa-google"></span></button>


              </div>
              <!-- <div class="form-group text-center">
                <button type="button" class="btn btn-primary btn-twitter"  @click="twitterAuth()"><span class="fa fa-twitter pr-3"></span> Login with Twitter</button>
              </div>
               <div class="form-group text-center">
                <button type="button" class="btn btn-primary btn-facebook" ><span class="fa fa-facebook pr-3"></span> Login with Facebook</button>
              </div>
               <div class="form-group text-center">
                <button type="button" class="btn btn-primary btn-google" ><span class="fa fa-google pr-3"></span> Login with Google</button>
              </div> -->
            </form>
          </div>
        </div>
      </div>

    </div>
  </div>
</template>


<script>
/* eslint-disable */
import { config } from '../firebase/index';
import { twitterAuth } from '../firebase/auth';
export default {
	name: 'auth',
	data() {
		return {
			msg: '',
		};
	},
	methods: {
		twitterAuth() {
			twitterAuth();
		},
	},
};
</script>


<style>
.btn-twitter {
	background-color: #03a9f4;
	border-color: #03a9f4;
}
.btn-facebook {
	background-color: #1e45d2;
	border-color: #1e45d2;
}
.btn-google {
	background-color: #ea0000;
	border-color: #ea0000;
}
.text {
	margin: auto;
}
#app {
	background: linear-gradient(#fff6f9, #ffe1eb);
	height: 100vh;
	/*text-align: center;*/
}
.logo-n {
	margin-top: 19vh;
}
</style>


